import { RotatingLines } from "react-loader-spinner";

const Loader = () => {
  return (
    <div className="loader-wrapper">
      <RotatingLines
        visible={true}
        height="96"
        width="96"
        color="grey"
        strokeColor="#4AB9EA"
        strokeWidth="5"
        animationDuration="0.75"
        ariaLabel="rotating-lines-loading"
        wrapperStyle={{}}
        wrapperClass=""
      />
    </div>
  );
};

export default Loader;
