import React, {useState, useEffect} from "react";
import Breadcrumb from "./Components/Breadcrumb";
import { Link } from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import placeholderImg from "../assets/images/placeholderImg.svg";
import bookLessonTime from "../assets/images/clock.svg";
import bookLessonCar from "../assets/images/car.svg";
import bookLessonCalendor from "../assets/images/calendor.svg";
import bookLessonMap from "../assets/images/map.svg";
import { useFormik } from "formik";
import { useNavigate } from 'react-router-dom';
//import * as Yup from "yup"; // For validation
import Swal from "sweetalert2"; 
import axios from "axios";

import { useLocation } from "react-router-dom";

import Loader from "./Components/Loader";
import { apiGet, apiPost, axiosWithAuth } from "../utilities/axiosInstance";
import { useAuth } from "../context/AuthContext";
import API_ENDPOINTS from "../utilities/apiEndPoints";

export default function Calendar() {
  const location = useLocation();
  const navigate = useNavigate();

  const { token } = useAuth();
  const [isLoading, setIsLoading]= useState(false);
  const [bookingID, setBookingID] = useState(location.state?.id || null);
  const [learnerDetails, setLearnerDetails]= useState({})
  const [instructorDetails, setInstructorDetail] = useState({})
  const [lessionDetail, setLessonDetail] = useState({})
  const [allDetails, setAllDetails] = useState({})

  const [trainerAvail, setTrainerAvail]= useState([])


//console.log(location.state?.id)
  // Initial values
  const formik = useFormik({
    initialValues: {
      booking_id:bookingID,
      lesson_date: lessionDetail?.lesson_date,
      lesson_time: lessionDetail?.lesson_time,
      
    },
 
    onSubmit: async (values) => {
      // Prepare the form data for submission
     

      try {
        setIsLoading(true)
        //  const response = await apiPost(`${API_ENDPOINTS.INSTRUCTOR_REGISTER}`, formData);
        const response = await axios.post(
          `${process.env.REACT_APP_API_BASE_URL}${API_ENDPOINTS.LEARNER_RESCHEDULE_BOOKING}`,
          {booking_id:values.booking_id,
            lesson_date:values.lesson_date || lessionDetail?.lesson_date,
            lesson_time:values.lesson_time || lessionDetail?.lesson_time,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
              "Content-Type": "application/json",
            },
          }
        );
        //console.log(response)
        //console.log(values);
        //const response = '';
        // Check if the response is valid
        if (!response || !response.data) {
          throw new Error("Invalid response format");
        }

        // Show SweetAlert on success
        Swal.fire({
          icon: "success",
          title: "Form Submitted Successfully!",
          text: "Your request has been submitted.",
        });

      } catch (error) {
        // Handle errors
       //console.log(error.response.data.data.error)
        Swal.fire({
          icon: "error",
          title: "Submission Failed",
          text:
          error.response.data.data.error || error.response.data.data || "Something went wrong. Please try again later.",
        });
      } finally {
        setIsLoading(false)
      }
    },
  });

// Update lesson_time whenever lesson_date changes
useEffect(() => {
  if (formik.values.lesson_date && trainerAvail.length > 0) {
    const filteredTrainer = trainerAvail.find(
      (item) => item.availability_date === formik.values.lesson_date
    );

    if (filteredTrainer) {
     // console.log("Setting lesson_time:", filteredTrainer.availability_start_time);
      formik.setFieldValue("lesson_time", filteredTrainer.availability_start_time);
    }
  }
}, [formik.values.lesson_date, trainerAvail]);


  const fetchTrainerAvailabilty = async (trainerID)=>{
    try{
      const trainerAvailResponse= await apiGet(`${API_ENDPOINTS.TRAINER_AVAILABILTY}/${trainerID}`);
     // console.log(trainerAvailResponse.data.processedAvailabilities)
      if(trainerAvailResponse.success){
         //  console.log(instructorPDPResponse.data)
         const filteredAvailability = trainerAvailResponse.data.processedAvailabilities.filter(
          (item) => item.lesson_hours === lessionDetail?.lesson_hours
        );

        if (filteredAvailability.length > 0) {
          setTrainerAvail(filteredAvailability);
        } else {
          console.warn("No matching instructor availability found");
        }

        //  setTrainerAvail(   trainerAvailResponse.data.processedAvailabilities.filter((item)=>{ //this is done for filter out the repeated date of availablty based on lesson hour
        //  return item.lesson_hours === lessionDetail?.lesson_hours
        //  }))
      }
    }
    catch(error){
     // console.log('Error fetching in instructor available', error)
    }
}

  useEffect(()=>{
   // console.log(bookingID)
   const fetchLearnerDashData = async () => {
    setIsLoading(true)
    try {
    const authInstance = axiosWithAuth(token); // Pass the token to create the instance
    const responseNew = await authInstance.get(`${API_ENDPOINTS.LEARNER_BOOKING_DETAIL}/${bookingID}`);
     const response = await responseNew.data
   //console.log(response)
      if (response.success && response.data) {
        setIsLoading(false)
        setAllDetails(response.data)
        setLearnerDetails(response.data.learners)
        setLessonDetail(response.data.lesson_details)
        setInstructorDetail(response.data.instructor)
       // Store instructor_id instead of immediately calling fetchTrainerAvailabilty
       if (response.data.instructor_id) {
        fetchTrainerAvailabilty(response.data.instructor.id);
      }
     else {
      console.error("Unexpected response format:", response);
           }
      } else {
        console.error("Unexpected response format:", response);
      }
    } catch (error) {
      console.error("Error fetching pricing data:", error);
    }
    finally{
      setIsLoading(false)
    }
  };

  if (bookingID) {
    fetchLearnerDashData();
  }

  },[bookingID, token])

  // Add a separate useEffect to handle trainer availability updates when lessionDetail changes
useEffect(() => {
  if (allDetails?.instructor?.id && lessionDetail?.lesson_hours) {
    fetchTrainerAvailabilty(allDetails?.instructor?.id);
  }
}, [allDetails?.instructor?.id, lessionDetail?.lesson_hours]);

  return (
    <div>
      {isLoading && <Loader />}
      <div className="mainContent">
        <Breadcrumb />
        <div className="mainContent__assets">
          <Container>
            <Row>
              <Col lg={12}>
                {/* Account setting */}
                <div className="personalDetailsForm">
                  <div className="d-flex justify-content-between align-items-center">
                    <h2>Reschedule a Booking</h2>

                    <div className="d-flex gap-2">
                      <button className="btn btn-grey-outline rounded-2 btn-sm" onClick={() => navigate(-1)}>
                        X DISCARD CHANGES
                      </button>
                      <Link to="/learner/make-another-booking" state={{ id: allDetails.instructor_id }} className="btn btn-grey rounded-2 btn-sm">
                        + PROPOSE NEW BOOKING
                      </Link>
                    </div>
                  </div>
                  {/* Calendar */}
                  <div className="bookingCard">
                    <div className="bookingCard__top">
                      <h4>Booking {allDetails?.bookingNumber} &nbsp;
                       
                        <div class={`badge ${allDetails.booking_status == 'confirmed' ? 'green' : 'red'}`}> { allDetails.booking_status }</div>
                      </h4>
                    </div>
                    <div className="bookingCard__mid">
                      <div className="bookingCard__profileWrp">
                        <div className="bookingCard__profile">
                          <div className="bookingCard__profileImg">
                            <img src={instructorDetails?.profile_picture || placeholderImg} alt="" />
                          </div>
                          <div className="bookingCard__profileInfo">
                            <div className="bookingCard__highlightedText">
                              <span>INSTRUCTOR DETAILS</span>
                            </div>

                            <strong>{ instructorDetails?.first_name} {instructorDetails?.last_name}</strong>
                            <Link>{instructorDetails?.contact_no}</Link>
                          </div>
                        </div>
                        <div className="bookingCard__profile">
                          <div className="bookingCard__profileImg">
                            <img src={instructorDetails?.instructor_vehicle?.vehicle_image || placeholderImg} alt="" />
                          </div>
                          <div className="bookingCard__profileInfo">
                            { instructorDetails?.instructor_vehicle?.vehicle_name} {instructorDetails?.instructor_vehicle?.vehicle_model} {instructorDetails?.instructor_vehicle?.vehicle_year}
                            
                            <span className="pdpWrapper__profileCarInfo__ncaprating">
                              <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                              <path d="M5 8.3747L7.42493 9.90932C7.86901 10.1906 8.41243 9.77481 8.29556 9.24901L7.65281 6.36318L9.79727 4.41892C10.1888 4.0643 9.9784 3.39176 9.4642 3.34896L6.64194 3.09829L5.53757 0.371428C5.33891 -0.123809 4.66109 -0.123809 4.46243 0.371428L3.35806 3.09217L0.535797 3.34285C0.021595 3.38565 -0.18876 4.05819 0.202734 4.4128L2.34719 6.35707L1.70444 9.24289C1.58757 9.7687 2.13099 10.1845 2.57507 9.90321L5 8.3747Z" fill="#FFD53E"/>
                              </svg> &nbsp;
                          {instructorDetails?.instructor_vehicle?.ancap_rating}-star ANCAP rating</span>
                          <span class="pdpWrapper__profileCarInfo__carno">{instructorDetails?.instructor_vehicle?.vehicle_no}</span>
                            <Link>{instructorDetails?.contact_no}</Link>
                          </div>
                        </div>
                      </div>
                      <div className="bookingCard__lessonWrp">
                        <div className="bookingCard__highlightedText">
                          <span>LESSON DETAILS</span>
                        </div>
                        <div className="bookingCard__lessonDetails">
                          <img src={bookLessonTime} alt="" />
                          {lessionDetail?.formatted_date}
                        </div>
                        <div className="bookingCard__lessonDetails">
                          <img src={bookLessonCar} alt="" />
                          <div className="badge">
                          {lessionDetail?.duration_transmission}
                          </div>
                        </div>
                        <div className="bookingCard__lessonDetails">
                          <img src={bookLessonCalendor} alt="" />
                          {lessionDetail?.formatted_time}
                        </div>
                        <div className="bookingCard__lessonDetails">
                          <img src={bookLessonMap} alt="" />
                          {learnerDetails?.address}
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="personalDetailsForm__wrp">
                    <div className="personalDetailsForm__top">
                      <h4>Booking </h4>
                    </div>

                    
                        <form className="ProfileForm RegistrationForm"  onSubmit={formik.handleSubmit}>
                          <fieldset>
                            <div className="form-group">
                              <label htmlFor="learnerName">*Learner Name</label>
                              <input
                                type="text"
                                name="learnerName"
                                id="learnerName"
                                className="form-control"
                                placeholder="James Smith - 040 313 4567"
                                value={learnerDetails.myself === 1 ? `${learnerDetails.first_name} ${learnerDetails.last_name}`: `${learnerDetails.learner_first_name} ${learnerDetails.learner_last_name}`}
                                disabled
                              />
                            </div>

                            <div className="form-group">
                              {/* driving lesson=> true, false, driving test package=> true , false */}
                              <label>*Booking Type</label>
                              <div className="booking-type-options">
                                <button type="button" className= {lessionDetail?.lesson_hours === 1 ? 'btn btn-teal' : 'btn'}>
                                  1-Hour Driving Lesson
                                </button>
                                <button type="button" className= {lessionDetail?.lesson_hours === 2 ? 'btn btn-teal' : 'btn'}>
                                  2-Hour Driving Lesson
                                </button>
                                <button type="button" className={lessionDetail?.test_package_id === 2 ? 'btn btn-teal' : 'btn'} >
                                  Driving Test Package
                                </button>
                              </div>
                            </div>

                            <div className="form-group">
                              <label>*Lesson Start Time</label>
                              <div className="row">
                                <div className="col-md-6">
                                  <select name="lesson_date" className="form-control" value={formik.values.lesson_date} onChange={formik.handleChange}>
                                    <option value={lessionDetail?.lesson_date}>{lessionDetail?.formatted_date}</option>
                                    {trainerAvail?.map((item, i)=>(
                                      <option key={i} value={item.availability_date}>{item.formatted_date}</option>
                                    ))}
                                    
                                    {/* Add options dynamically */}
                                  </select>
                                </div>
                                <div className="col-md-6">
                                  <select  name="lesson_time" className="form-control" value={formik.values.lesson_time} onChange={formik.handleChange}>
                                    <option value={lessionDetail?.lesson_time}>{lessionDetail?.lesson_time}</option>
                                    {/* {trainerAvail?.map((item, i)=>(
                                      <option key={i} value={item.availability_start_time}>{item.availability_start_time}</option>
                                    ))} */}
                                    {trainerAvail
                                  ?.filter(item => item.availability_date === formik.values.lesson_date) // Assuming item.date is in "YYYY-MM-DD" format
                                  .map((item, i) => (
                                  <option key={i} value={item.availability_start_time}>
                                  {item.availability_start_time} - {item.availability_end_time}
                                  </option>
                                  ))}
                                    {/* Add options dynamically */}
                                  </select>
                                </div>
                              </div>
                            </div>

                            <div className="form-group">
                              <label>*Lesson Pick Up Location</label>
                              <div className="location-display position-relative">
                                <input
                                  type="text"
                                  className="form-control"
                                  value={learnerDetails?.address}
                                  disabled
                                />
                                {/* <button type="button" className="btn btn-link">
                                  <svg
                                    width="11"
                                    height="12"
                                    viewBox="0 0 11 12"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                  >
                                    <path
                                      d="M0 11.251V9.2054H11V11.251H0ZM2.2 7.15975H2.97L7.26 3.18353L6.47625 2.45477L2.2 6.44378V7.15975ZM1.1 8.18258V6.00908L7.26 0.294061C7.36083 0.200303 7.47771 0.127853 7.61063 0.0767116C7.74354 0.0255705 7.88333 0 8.03 0C8.17667 0 8.31875 0.0255705 8.45625 0.0767116C8.59375 0.127853 8.7175 0.204564 8.8275 0.306847L9.58375 1.02282C9.69375 1.11658 9.77396 1.22739 9.82438 1.35524C9.87479 1.48309 9.9 1.61521 9.9 1.75158C9.9 1.87944 9.87479 2.00516 9.82438 2.12875C9.77396 2.25234 9.69375 2.36528 9.58375 2.46756L3.4375 8.18258H1.1Z"
                                      fill="#1C1B1F"
                                    />
                                  </svg>
                                  Edit
                                </button> */}
                              </div>

                              {/* <div className="row mt-3">
                                <div className="col-md-4">
                                  <Field
                                    type="text"
                                    name="streetNumber"
                                    className="form-control"
                                    placeholder="Street No."
                                  />
                                </div>
                                <div className="col-md-4">
                                  <Field
                                    type="text"
                                    name="streetName"
                                    className="form-control"
                                    placeholder="Street Name"
                                  />
                                </div>
                                <div className="col-md-4">
                                  <Field
                                    type="text"
                                    name="street"
                                    className="form-control"
                                    placeholder="Street"
                                  />
                                </div>
                              </div> */}

                              {/* <div className="form-group mt-3">
                                <Field
                                  type="text"
                                  name="alternateLocation"
                                  className="form-control"
                                  placeholder="Alexander Heights WA 6064"
                                />
                              </div> */}
                            </div>

                            <p className="form-note">
                              Booking proposals will "hold" the selected time
                              slot(s) until confirmed by the learner. Proposals
                              remain valid for 24 hours OR proposed booking
                              start time (whichever arrives first).
                            </p>
                            <div class="d-flex justify-content-end personalDetailsForm__btnWrapper gap-4 flex-wrap ">
                              <button class="btn btn-teal updateChanges" type="submit" disabled={isLoading}>Change schedule</button>
                            </div>
                          </fieldset>
                        </form>
                     
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </div>
  );
}
